import React, { Component } from 'react';
import {
    Route,
    Link
} from 'react-router-dom';

const Home = () => (
    <div>Home</div>
);
const About = () => (
    <div>About</div>
);

const App = () => (
    <div>
        <ul>
            <li><Link to="/">Home</Link></li>
            <li><Link to="/about">About</Link></li>
        </ul>
        <Route path="/" exact component={Home} />
        <Route path="/about" component={About} />
    </div>
);
export default App;



